import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import Remind from 'UNIT_PATH/remind';
import Loading from 'UNIT_PATH/loading';
const model = require('UNIT_PATH/model');
const view = require('UNIT_PATH/view');

let redCoupon = $('#red-coupon');

function main() {
	setTimeout(function() {
		getCouponStatus();
	}, 20000);

	setTimeout(function() {
		getCouponStatus();
	}, 40000);

	model.getCouponsDom(function(data) {
		// 插入优惠券dom
		redCoupon.html(data);

		if(!$('#red-coupon').children().length) {
			return $('#red-coupon').remove();
		}

		renderCouponsType();
		redCouponBindEvent();
		Loading.showLoading({
            type: 'load-fullscreen',
            loadingTxt: ''
        }, function(_loadingHtml) {
            $('body').append(_loadingHtml);
        });
        getCouponStatus();
	});
}

function renderCouponsType() {
	$('#red-coupon .coupon-col').each(function(i, v) {
		var _this = $(this);

		if(_this.attr('data-type') == 1) {
			_this.addClass('normal');
		}else if(_this.attr('data-type') == 2) {
			_this.addClass('baitiao');
			_this.find('.name').append('<strong class="coupon-type">白条券</strong>');
		}else if(_this.attr('data-type') == 3) {
			_this.find('.name').append('<strong class="coupon-type">京东支付券</strong>');
		}
	});
}

function redCouponBindEvent() {
	$('#red-coupon a').bind('click', function() {
		var _this = this;

		if($(_this).hasClass('timeout')) {
			Remind._normalRemind('不能重复领取');
		}else{
			// 发送领取优惠券请求
			model.getCouponAct(_this, function(data) {
				view.renderActCouponsGet(_this, data);
			});
		}
	});
}

function getCouponStatus() {
	model.getCouponInfo(function(data) {
		view.renderCouponsStatus(data);
	});

	model.getBtStatus(function(data) {
		view.renderCouponsbtStatus(data);
	});
}

module.exports = main;


